  "What is news, it is not what is true. It is what people want to believe. And it is the people who decide whats true and what they want. And, of course, thats why we have all of those experts on TV. Theyre the ones who know whats best. And theyre not experts. They are in fact, part of the problem. They arent looking at the facts. They dont even know what the facts are. All they know is what they think the public wants to hear. Thats what makes them experts. To a lot of people in the world, what they are interested in is whats going on in the news. The news is the most important source of information they have for their lives. They believe it to be the only way to know what is true and what is not. They dont trust anything else. They rely on the information that the news gives them for life.";
